export default {
	data() {
		return {
			swipeImgUrl: [],
			hotImg: '../../static/icos/hot.png',
		}
	},
	onLoad() {
		// TODO 请求轮播图数据
		uni.request({
			url: `${this.serverURL}/index/carousel/list`,
			method: "POST",
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			data: {qq: this.qq},
			success: (res) => {
				if (res.data.status === 200) {
					this.swipeImgUrl = res.data.data;
				}

			}
		});
	},
	methods: {

	}
}
